import React, { useState } from 'react';
import Livros from './pages/Livros';
import Leitores from './pages/Leitores';
import Emprestimos from './pages/Emprestimos';
import './styles/App.css';

function App() {
  const [telaAtiva, setTelaAtiva] = useState('livros');

  const renderizarTela = () => {
    switch (telaAtiva) {
      case 'livros':
        return <Livros />;
      case 'leitores':
        return <Leitores />;
      case 'emprestimos':
        return <Emprestimos />;
      default:
        return <Livros />;
    }
  };

  return (
    <div className="app">
      <header className="header">
        <h1>Sistema de Biblioteca</h1>
        <nav className="nav">
          <button 
            className={`nav-btn ${telaAtiva === 'livros' ? 'active' : ''}`}
            onClick={() => setTelaAtiva('livros')}
          >
            Livros
          </button>
          <button 
            className={`nav-btn ${telaAtiva === 'leitores' ? 'active' : ''}`}
            onClick={() => setTelaAtiva('leitores')}
          >
            Leitores
          </button>
          <button 
            className={`nav-btn ${telaAtiva === 'emprestimos' ? 'active' : ''}`}
            onClick={() => setTelaAtiva('emprestimos')}
          >
            Empréstimos
          </button>
        </nav>
      </header>

      <main>
        {renderizarTela()}
      </main>

      <footer style={{
        textAlign: 'center',
        padding: '20px',
        color: 'white',
        marginTop: '40px'
      }}>
        <p>Sistema de Biblioteca</p>
      </footer>
    </div>
  );
}

export default App;